<script>
	import { onMount } from 'svelte';
	const END_POINT = 'https://quizzrapi.herokuapp.com/random';
	const CORS_PROXY_ENDPOINT = 'https://cors-anywhere.herokuapp.com/';
	let question = '';
	let answer = '';
	let choice = '';	
	let correct = '👍';
	let incorrect = '👎';
	let isRight = '';
	let numQuestions = 0;
	let score = 0;
	
	let answers = [];
	
	onMount(() => {
		getQuestion();
	});
	
	function getQuestion() {
		fetch(`${CORS_PROXY_ENDPOINT}${END_POINT}`)
		.then(resp => resp.json())
		.then(json => init(json))
		.catch(err => console.log(err.message)); 
	}
	
	function init(obj) {
		question = obj.question;
		answer = obj.answer;		
		answers = [
			{"letter": 'A', "value": obj.a},
			{"letter": 'B', "value": obj.b},
			{"letter": 'C', "value": obj.c},
			{"letter": 'D', "value": obj.d}							
		];
		answers.sort((i, j) => {
			return Math.random() - 0.5;
		});
		isRight = '';
		choice = '';
	}	
	
	function checkAnswer() {		
		if (choice === answer) {
			score++;
			isRight = correct;
		} else {
			isRight = incorrect;
		}
		numQuestions++;
	}
</script>
<h1>
	Quiz
</h1>
<p>API from: <a href="https://quizzrapi.herokuapp.com/">https://quizzrapi.herokuapp.com/</p>
<h2>Question</h2>
<p class="question">
	{question}
</p>	
<h2>Answers</h2>
<ul>
	{#each answers as answer, id(answer.letter)}
		<li><strong>{answer.letter.toUpperCase()}</strong>: {answer.value}</li>
	{/each}	
</ul>
<div>
	<strong>Answer</strong>: <input id="foo" type="text" bind:value={choice} >&nbsp;
	<strong>Result</strong>: <span class="is-right">{isRight}</span>	
</div>
<div>
	<strong>Score</strong>: {score}/{numQuestions}		
</div>	
<div style="margin-top: 2rem;">
	<input type="button" value="Submit" on:click={checkAnswer}/>
	<input type="button" value="Next Question" on:click={getQuestion}/>
</div>

